import React, {useState} from 'react';
import style from "./Paginator.module.css"
// import cn from "classnames";

type PropsType  = {
    totalUserCount : number
    pageSize: number
    currentPage : number
    onPageChanged : (pageNumber : number) => void
    portionSize? : number
}

    let Paginator : React.FC<PropsType> = ({totalUserCount,
                                           pageSize,
                                           currentPage,
                                           onPageChanged,
                                           portionSize = 20}) => {
    let pagesCount = Math.ceil(totalUserCount / pageSize);
    let pages : Array<number> = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }
    let portionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionPageNumber = portionNumber * portionSize;


    return (
    <div className={style.paginator}>
            {portionNumber > 1 &&
            <button className={`${style.buttonLeft} ${style.buttonClick}`} onClick={() => {
                setPortionNumber(portionNumber - 1)
            }}>PREV</button>}

            {pages
                .filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
                .map((p) => {
                    return <span className={currentPage === p ? style.selectedPage : style.pageNumber}
                                 key={p}
                                 onClick={(e) => {
                                     onPageChanged(p);
                                 }}>{p}</span>
                })}
            {portionCount > portionNumber &&
            <button className={`${style.buttonRight} ${style.buttonClick}`} onClick={() => {
                setPortionNumber(portionNumber + 1)
            }}>NEXT</button>}
        </div>
    )
}

export default Paginator;